.unit {
    margin: 18px;
}

.unit h2 {
    padding: 18px;
    padding-top: 0;
    font-weight: bold;
}

.unit ul {
    margin: 18px;
    list-style-type: none;
}

.unit ul .subunit {
    text-decoration: none;
    font-size: 18px;
    display: inline-block;
    position: relative;
}

.unit ul .subunit:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: rgb(75, 156, 255);
    transform-origin: bottom right;
    transition: transform 0.5s ease-out;
}

.unit ul .subunit:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.unit p article {
    padding: 24px;
    width: 50%;
    border: 2px solid;
    border-radius: 20px;
    margin: auto;
    line-height: 22px;
}

.unit1Img {
    display: block;
    margin: auto;
    width: 40%;
}